<template>
  <div>
    <Header />
    <b-container fluid="md">
      <p>.<span v-html="strHtml"></span></p>
      <h6> </h6>
      <p>.<span v-html="strHtml"></span></p>
      <b-form v-if="show">
        <b-form-group id="input-group-1" label="" label-for="input-1">
          <b-form-input
            id="input-1"
            v-model="project.prj_title"
            placeholder=""
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group label=" " v-slot="{ ariaDescribedby }">
          <b-form-radio-group
            id="radio-group-2"
            v-model="project.cond"
            :aria-describedby="ariaDescribedby"
            name="radio-sub-component"
          >
            <b-form-radio value=" "> </b-form-radio>
            <b-form-radio value=""></b-form-radio>
            <b-form-radio value=" "> </b-form-radio>
          </b-form-radio-group>
        </b-form-group>

        <b-form-group id="input-group-1" label=" " label-for="input-1">
          <b-form-datepicker
            id="input-1"
            v-model="project.start_d8"
            placeholder=" "
            required
          ></b-form-datepicker>
        </b-form-group>

        <b-form-group id="input-group-1" label=" " label-for="input-1">
          <b-form-datepicker
            id="input-1"
            v-model="project.end_d8"
            placeholder=" "
            required
          ></b-form-datepicker>
        </b-form-group>

        <b-form-group id="input-group-1" label="" label-for="input-1">
          <b-form-input
            id="input-1"
            v-model="leader.email"
            placeholder=""
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          id="input-group-1"
          label="  "
          label-for="input-1"
        >
          <b-form-datepicker
            id="input-1"
            v-model="leader.prj_in_d8"
            placeholder="  "
            required
          ></b-form-datepicker>
        </b-form-group>

        <b-form-group
          id="input-group-1"
          label="  "
          label-for="input-1"
        >
          <b-form-datepicker
            id="input-1"
            v-model="leader.prj_out_d8"
            placeholder="  "
            required
          ></b-form-datepicker>
        </b-form-group>

        <b-form-group id="input-group-1" label="" label-for="input-1">
          <b-form-input
            id="input-1"
            v-model="follower.email"
            placeholder=""
            required
          ></b-form-input>
        </b-form-group>

        <b-form-group
          id="input-group-1"
          label="  "
          label-for="input-1"
        >
          <b-form-datepicker
            id="input-1"
            v-model="follower.prj_in_d8"
            placeholder="  "
            required
          ></b-form-datepicker>
        </b-form-group>

        <b-form-group id="input-group-1" label=" " label-for="input-1">
          <b-form-datepicker
            id="input-1"
            v-model="follower.prj_out_d8"
            placeholder="  "
            required
          ></b-form-datepicker>
        </b-form-group>
        <div id="add" class="text-center">
          <b-button variant="secondary" @click="add"></b-button>
        </div>
        <p>.<span v-html="strHtml"></span></p>

        <b-form-group id="input-group-1" label="" label-for="input-1">
          <b-form-textarea
            id="input-1"
            v-model="project.prj_content"
            placeholder=" "
            rows="8"
            required
          ></b-form-textarea>
        </b-form-group>

        <b-form-group id="input-group-1" label="" label-for="input-1">
          <b-form-textarea
            id="input-1"
            v-model="project.prj_remark"
            placeholder=""
            rows="4"
            required
          ></b-form-textarea>
        </b-form-group>
        <p>.<span v-html="strHtml"></span></p>

        <b-button type="button" variant="primary" @click="prjWrite"
          ></b-button
        >
        <b-button type="button" variant="danger" @click="reset"></b-button>
        <b-button variant="info" href="/prjList"></b-button>
      </b-form>
      <p>.<span v-html="strHtml"></span></p>
      <p>.<span v-html="strHtml"></span></p>
      <Footer />
    </b-container>
  </div>
</template>

<script>
import Header from "../../views/common/00_header"; //import  
import Footer from "../../views/common/90_footer"; //import  

export default {
  name: "prjWrite",
  components: {
    Header, //  
    Footer, //  
  },
  data() {
    return {
      project: {
        prj_title: "",
        cond: "",
        start_d8: "",
        end_d8: "",
        prj_content: "",
        prj_remark: "",
      },
      leader: {
        email: "",
        prj_in_d8: "",
        prj_out_d8: "",
      },
      follower: {
        email: "",
        prj_in_d8: "",
        prj_out_d8: "",
      },
      show: true,
    };
  },
  methods: {
    prjWrite() {
      alert(JSON.stringify(this.project));
      var leader = {
        email: this.leader.email,
        prj_in_d8: this.leader.prj_in_d8,
        prj_out_d8: this.leader.prj_out_d8,
      };
      var follower = {
        email: this.follower.email,
        prj_in_d8: this.follower.prj_in_d8,
        prj_out_d8: this.follower.prj_out_d8,
      };
      var project = {
        prj_title: this.project.prj_title,
        cond: this.project.cond,
        start_d8: this.project.start_d8,
        end_d8: this.project.end_d8,
        prj_content: this.project.prj_content,
        prj_remark: this.project.prj_remark,
        leader: leader,
        follower: follower,
      };

      this.$axios
        .post("/prjWrite", JSON.stringify(project), {
          headers: {
            "Content-Type": "application/json",
          },
        })
        .then((res) => {
          console.log(res);
          alert("  ");
          this.$router.push("/prjList");
        });
    },
    reset(event) {
      event.preventDefault();
      // Reset our form values
      this.project.prj_title = "";
      this.project.cond = "";
      this.project.start_d8 = "";
      this.project.end_d8 = "";
      this.project.prj_content = "";
      this.project.prj_remark = "";
      this.leader.email = "";
      this.leader.prj_in_d8 = "";
      this.leader.prj_out_d8 = "";
      this.follower.email = "";
      this.follower.prj_in_d8 = "";
      this.follower.prj_out_d8 = "";
      // Trick to reset/clear native browser form validation state
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
      });
    },
  },
};
</script>

